// Prisma Client Singleton for Next.js
// Detta fÃ¶rhindrar flera instanser i development med hot reload

const { PrismaClient } = require('@prisma/client');

let prisma;

if (process.env.NODE_ENV === 'production') {
  prisma = new PrismaClient({
    log: ['error', 'warn'],
  });
} else {
  // I development, anvÃ¤nd global fÃ¶r att Ã¶verleva hot reload
  if (!global.prisma) {
    global.prisma = new PrismaClient({
      log: ['query', 'error', 'warn'],
    });
  }
  prisma = global.prisma;
}

// Kontrollera om databas Ã¤r aktiverad
const isDatabaseEnabled = process.env.ENABLE_DATABASE === 'true';

// Exportera bÃ¥de client och status
module.exports = {
  prisma: isDatabaseEnabled ? prisma : null,
  isDatabaseEnabled
};